// !!! create luckysheet after mounted
onMounted(async () => {
  const id =siyuan.currentNodeId()
  const r = await siyuan.getBlockAttr(id, "custom-data")
  console.log("getBlockAttr",r)
  if (r && r.value != "") {
    sheetData.value = JSON.parse(r.value)
    const operate={...defaultOptions.value,data:sheetData.value}
    luckysheet.create(operate)
  }else{
    const operate={...defaultOptions.value,...defaultData.value}
    console.log(operate)
    luckysheet.create(operate)
  }
})
