import {util, api} from "siyuan_api_cache_lib";

// ID
const currentNodeId = () => {
    console.log(import.meta.env.DEV)
    if (import.meta.env.DEV) {
        return "block-id"
    } else {
        return util.currentNodeId()
    }

};


//
const getBlockAttr = async (id: string, key: string) => {
    if (import.meta.env.DEV) {
        var attrs = sessionStorage.getItem(id);
        if (attrs){
            var parse = JSON.parse(attrs);
            let value=parse[key];
            if (value){
                return {"value":value};
            }
            return null;
        }
        return null;
    } else {
        return await api.getBlockAttr(id, key);
    }
}


//
const setBlockAttrs = async (data: {
    /**  ID */
    id: string;
    attrs: {
        [key: string]: string;
    };
}) => {
    if (import.meta.env.DEV) {
        // @ts-ignore
        sessionStorage.setItem(data.id, JSON.stringify(data.attrs))
    } else {
        return await api.setBlockAttrs(data);
    }
}

export const siyuan = {
    currentNodeId: () => {
        console.log(process.env);
        // ID
        return 'some_node_id';
    },
};
export default {
    currentNodeId,
    getBlockAttr,
    setBlockAttrs,
};
